import React from 'react';
import { Container, Header, Message } from 'semantic-ui-react';
import Search from './Search';
import Trending from './Trending';
import './css/App.css';

class App extends React.Component {
  constructor() {
    super();
    this.state = { apiKey: true }
  }
  
  componentWillMount() {
    if (!process.env.REACT_APP_API_KEY) this.setState({ apiKey: false });
  }

  render() {

    return (
      <Container className="app" textAlign="center" fluid>
        { !this.state.apiKey ? (
          <Message negative>
            <Message.Header>Sorry, can't display this page.</Message.Header>
            <p>API Key not found :(</p>
          </Message>
        ) : (
          <div>
            <Header as="h1" size="large">Marvel Giphy</Header>
            <Search />
            <Trending />
          </div>
        )}
      </Container>
    );
  }
}

export default App;



